// component for the chat interface

'use client';

import { useState, useEffect } from 'react';
import { useChat } from '@/contexts/ChatContext';
import { useThread } from '@/contexts/ThreadContext';
import { useChatActions } from '@/hooks/useChatActions';
import { ChatThread } from './chat-thread';
import { ChatWindow } from './chat-window';
import { DebugPanel } from './debug-panel';
import { Menu, X, Bug } from 'lucide-react';
import { cn } from '@/lib/utils';
import { motion, AnimatePresence } from 'framer-motion';

const springTransition = {
  type: "spring",
  stiffness: 300,
  damping: 30
};

export function Chat() {
  const { messages, isLoading, rawMessages } = useChat();
  const { sendMessage, ready } = useChatActions();
  const { threads, currentThreadId, createNewThread, loadThreadHistory, setCurrentThreadId } = useThread();
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const [isDebugOpen, setIsDebugOpen] = useState(false);

  const handleNewChat = async () => {
    try {
      const newThreadId = await createNewThread();
      setCurrentThreadId(newThreadId);
    } catch (error) {
      console.error('Error creating new chat:', error);
    }
  };

  const handleSelectThread = async (threadId: string) => {
    try {
      setCurrentThreadId(threadId);
      await loadThreadHistory(threadId);
    } catch (error) {
      console.error('Error loading thread:', error);
    }
  };

  // Load thread history when currentThreadId changes
  useEffect(() => {
    if (currentThreadId) {
      loadThreadHistory(currentThreadId).catch(console.error);
    }
  }, [currentThreadId]);

  return (
    <div className="flex h-screen bg-[#1A1A1A] relative overflow-hidden">
      {/* Backdrop for mobile sidebar */}
      <AnimatePresence>
        {isSidebarOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 0.4 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black md:hidden z-20"
            onClick={() => setIsSidebarOpen(false)}
          />
        )}
      </AnimatePresence>

      {/* Sidebar */}
      <motion.div
        initial={false}
        animate={{
          width: isSidebarOpen ? '320px' : '0px',
          x: isSidebarOpen ? 0 : -320
        }}
        transition={springTransition}
        className="fixed md:relative h-full z-30 overflow-hidden"
      >
        <ChatThread
          threads={threads}
          onNewChat={handleNewChat}
          onSelectThread={handleSelectThread}
          activeThreadId={currentThreadId || undefined}
        />
      </motion.div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col min-w-0 h-screen relative">
        {/* Header */}
        <div className="flex-none h-14 border-b border-[#262626] bg-[#1A1A1A]/80 backdrop-blur-md supports-[backdrop-filter]:bg-[#1A1A1A]/60 z-10">
          <div className="max-w-screen-2xl mx-auto px-4 h-full flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={() => setIsSidebarOpen(!isSidebarOpen)}
                className="p-2 hover:bg-[#262626] rounded-lg transition-all duration-200 ease-in-out"
              >
                <motion.div
                  animate={{ rotate: isSidebarOpen ? 0 : 180 }}
                  transition={{ duration: 0.2 }}
                >
                  {isSidebarOpen ? <X className="w-5 h-5 text-gray-400" /> : <Menu className="w-5 h-5 text-gray-400" />}
                </motion.div>
              </button>
              <div className="text-gray-200 font-medium">
                {currentThreadId ? threads.find(t => t.id === currentThreadId)?.title : 'New Chat'}
              </div>
            </div>
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => setIsDebugOpen(!isDebugOpen)}
              className="px-3 py-1.5 text-sm text-gray-400 hover:bg-[#262626] rounded-lg transition-colors flex items-center gap-2"
            >
              <Bug className="w-4 h-4" />
              <span className="hidden sm:inline">{isDebugOpen ? 'Hide Debug' : 'Show Debug'}</span>
            </motion.button>
          </div>
        </div>

        {/* Chat and Debug Panel Container */}
        <motion.div 
          className="flex-1 overflow-hidden flex"
          layout
        >
          {/* Chat Window */}
          <motion.div
            layout
            className="flex-1 overflow-hidden"
            animate={{
              width: isDebugOpen ? '50%' : '100%'
            }}
            transition={springTransition}
          >
            <ChatWindow
              messages={messages}
              isLoading={isLoading}
              onSendMessage={sendMessage}
              isReady={ready && !!currentThreadId}
            />
          </motion.div>

          {/* Debug Panel */}
          <motion.div
            layout
            initial={{ width: 0 }}
            animate={{
              width: isDebugOpen ? '50%' : 0
            }}
            transition={springTransition}
            className="overflow-hidden"
          >
            {isDebugOpen && <DebugPanel messages={rawMessages || []} />}
          </motion.div>
        </motion.div>
      </div>
    </div>
  );
} 